import React from 'react';
import Button from "../buttons/Button";
// import PropTypes from "prop-types";
import './ChartHeaderA.scss'

function ChartHeaderA(props) {
    const {children}= props;
    return (
        <div className={`cMainWrap`}>
            <div className={`headerWrapper`}>
                <div>
                    <h4>Title</h4>
                    <p>Some text about this chart</p>
                </div>
                <div>
                    <Button/>
                </div>
            </div>
            <div>
                {children}
            </div>
        </div>
    );
}

