import React, {useState} from 'react';
import './TopMenuA.scss'
import { FiMenu } from "react-icons/fi";
import { FiX } from "react-icons/fi";


function TopMenuA(props) {

    const[isopen, setIsOpen]= useState(false);

    const toggleMenu = ()=>{
        setIsOpen(!isopen)
    }

    return (
       <>
           <div className={`mainWrapper`}>
               <div className={`iconContainer`} onClick={toggleMenu}> {isopen ? <FiX/>  :<FiMenu/>  }</div>
               <h3>Advantage IQ</h3>
           </div>
           <div onClick={toggleMenu}  className={[`sideMenuWrapper`, isopen ? `sideMenuWrapper_open`:`sideMenuWrapper_close`].join(' ')}>
               <p>side Menu</p>
           </div>
       </>

    );
}

export default TopMenuA;